<template>
  <div class="tooltip-container">
    <slot />
    <div class="tooltip">
      <span class="text">{{ description }}</span>
    </div>
  </div>
</template>

<script>
import axios from "axios";

export default {
  data: () => ({
    description: "",
  }),
  props: {
    type: {
      type: String,
      required: true,
    },
    code: {
      type: String,
      required: true,
    },
  },
  mounted() {
    this.updateDescription();
  },
  watch: {
    code(newVal, oldVal) {
      this.updateDescription();
    },
  },
  methods: {
    async updateDescription() {
      let url = `https://clinicaltables.nlm.nih.gov/api/icd10cm/v3/search?terms=${this.code}`; // default to icd10
      if (this.type == "cpt") {
        url = `https://clinicaltables.nlm.nih.gov/api/hcpcs/v3/search?terms=${this.code}`;
      }

      if (!this.code) {
        // no API call
        return;
      }

      try {
        const response = await axios.get(url);

        console.log("response - ", response.data[3]);
        if (response.data[3].length == 0) {
          this.description = "No Description Found";
        } else if (response.data[3].length == 1) {
          this.description = response.data[3][0][1];
        } else {
          // TODO - what to do with multiple codes returned
          this.description = "Multiple values returned";
        }
      } catch (e) {
        console.log("err - ", e);
        this.description = "There was a problem retrieving the description.";
      }
    },
  },
};
</script>

<style scoped>
.tooltip-container {
  position: relative;
  display: inline-block;
  /* margin: 100px; */
}
.tooltip-container:hover .tooltip {
  opacity: 1;
}
.tooltip {
  color: #ffffff;
  text-align: center;
  padding: 5px 12px;
  border-radius: 2px;
  width: 250px;
  bottom: 100%;
  left: 50%;
  margin-left: -125px;
  opacity: 0;
  transition: opacity 1s;
  position: absolute;
  z-index: 1;
  background: #000000;
}
.text::after {
  content: " ";
  position: absolute;
  top: 100%;
  left: 50%;
  margin-left: -5px;
  border-width: 5px;
  border-style: solid;
  border-color: #000000 transparent transparent transparent;
}
</style>
